/* eslint-disable react/jsx-key */
import Axios from 'axios';
import React, { Component, Fragment } from 'react';
import filterSingleGame from '../utils/filterSingleGame';

class root extends Component {
 constructor() {
  super();
  let staticDate = new Date();
  staticDate = staticDate.toLocaleDateString();
  let header = new Date();
  header.setDate(header.getDate() - 1);
  this.state = {
   dayOffSet: -1,
   staticDate,
   dateHeader: header.toLocaleDateString(),
   preferences: {
    GAME_DATE_EST: false,
    GAME_SEQUENCE: false,
    GAME_ID: false,
    TEAM_ID: false,
    TEAM_ABBREVIATION: true,
    TEAM_CITY_NAME: false,
    TEAM_WINS_LOSSES: false,
    PTS_QTR1: true,
    PTS_QTR2: true,
    PTS_QTR3: true,
    PTS_QTR4: true,
    PTS_OT1: false,
    PTS_OT2: false,
    PTS_OT3: false,
    PTS_OT4: false,
    PTS_OT5: false,
    PTS_OT6: false,
    PTS_OT7: false,
    PTS_OT8: false,
    PTS_OT9: false,
    PTS_OT10: false,
    PTS: true,
    FG_PCT: true,
    FT_PCT: false,
    FG3_PCT: false,
    AST: false,
    REB: false,
    TOV: false,
   },
  };
 }

 async componentDidMount() {
  let { data } = await Axios.get('http://localhost:8001/scoreboard', {
   params: {
    GameDate: this.state.staticDate,
    LeagueID: '00',
    DayOffset: this.state.dayOffSet,
   },
  });
  this.setState({ data });
 }
 async handleDayChange(e) {
  let change = parseInt(e.target.value, 10);
  let { data } = await Axios.get('http://localhost:8001/scoreboard', {
   params: {
    GameDate: this.state.staticDate,
    LeagueID: '00',
    DayOffset: this.state.dayOffSet + change,
   },
  });
  let day = new Date();
  day.setDate(day.getDate() + this.state.dayOffSet + change);
  this.setState({
   data,
   dayOffSet: this.state.dayOffSet + change,
   dateHeader: day.toLocaleDateString(),
  });
 }

 render() {
  let games = [];
  if (this.state.data) {
   let data = this.state.data;
   let preferences = this.state.preferences;
   games = filterSingleGame(data, preferences);
   console.log(games);
  }
  return (
   <Fragment>
    <h1> {this.state.dateHeader} </h1>
    <div>
     <button
      type="button"
      value="-1"
      onClick={() => this.handleDayChange(event)}
     >
      prev
     </button>
     <button
      type="button"
      value="1"
      onClick={() => this.handleDayChange(event)}
     >
      next
     </button>
    </div>
    <br />
    {games.map(game => {
     return Object.keys(game.team1).map(header => {
      return (
       <div>
        {header}: {game.team1[header]}
       </div>
      );
     });

     /* //   <Fragment>
     //    <div>
     //     {game.team1.TEAM_ABBREVIATION}: {game.team1.PTS}
     //    </div>
     //    <div>
     //     {game.team2.TEAM_ABBREVIATION}:{game.team2.PTS}
     //    </div>
     //    <br />
     //   </Fragment> */
    })}
   </Fragment>
  );
 }
}

export default root;
